.project-heading{
    text-align: center;
    padding: 4rem 0 2rem 0;
}

.project-container{
    max-width: 1140px;
    margin: auto;
    display: grid;
    grid-template-columns: repeat(3,1fr);
    grid-gap: 40px;
    padding-bottom: 5rem;
}

.project-card{
    background: #1a1919;
    padding: 1.2rem 1rem;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
}

.project-card img{
    width: 100%;
    height: 200px;
}

.project-title{
    color: #fff;
    padding: 1rem 0; 
}

.project-detais{
    padding-bottom: 1rem;
    font-size: 1.1rem;
    text-align: justify;
    flex-grow: 1;
}

.pro-btns{
    display: flex;
    justify-content: space-between;
    align-items: flex-end;
    padding: 0.5rem 0;
}

.pro-btns .btn{
    padding: 0.5rem 3rem;
}


@media screen and (max-width: 740px){
    .project-container{
        max-width: 90%;
        margin: auto;
        grid-template-columns: 1fr;
    }
}
