.footer{
    width:100%;
    padding: 3rem 0;
    background-color: rgba(19,19,19,0.8);
}

.footer-container{
    max-width: 1140px;
    margin: auto;
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    grid-gap: 40px;
}

.footer-left{
    height: 100%;
    max-width: 100%;   
    display: flex;
    flex-direction: column;
    padding: 1rem;
}

.location{
    display: flex;
    margin-bottom: 0.8rem;
}

.location p{    
    line-height: 30px;
}

.right{
    height: 100%;
    max-width: 100%;
    display: flex;
    flex-direction: column;
    padding: 1rem;
}

.socials{
    margin-top: 1rem;
}

/* make socials hover */

.socials {
  display: flex;
  align-items: center;
}

.socials > * {
  margin-right: 1rem;
  font-size: 1.5rem;
  color: #fff;
  transition: transform 0.3s ease-in-out;
}

.socials > *:hover {
  transform: scale(1.2);
}


h4{
    font-size: 1.2rem;
    padding-bottom: 0.7rem;
}

p{
    font-size: 1.2rem;
}

@media screen and (max-width: 640px) {
    .footer-container{
        grid-template-columns: 1fr; 
    }
}